// SCSS variables are information about icon's compiled state, stored under its original file name
//
// .icon-home {
//   width: $icon-home-width;
// }
//
// The large array-like variables contain all information about a single icon
// $icon-home: x y offset_x offset_y width height total_width total_height image_path;
//
// At the bottom of this section, we provide information about the spritesheet itself
// $spritesheet: width height image $spritesheet-sprites;
$icon-01-name: 'icon_01';
$icon-01-x: 0px;
$icon-01-y: 0px;
$icon-01-offset-x: 0px;
$icon-01-offset-y: 0px;
$icon-01-width: 42px;
$icon-01-height: 25px;
$icon-01-total-width: 84px;
$icon-01-total-height: 82px;
$icon-01-image: '../images/sprite.png';
$icon-01: (0px, 0px, 0px, 0px, 42px, 25px, 84px, 82px, '../images/sprite.png', 'icon_01', );
$icon-02-name: 'icon_02';
$icon-02-x: 0px;
$icon-02-y: 50px;
$icon-02-offset-x: 0px;
$icon-02-offset-y: -50px;
$icon-02-width: 32px;
$icon-02-height: 32px;
$icon-02-total-width: 84px;
$icon-02-total-height: 82px;
$icon-02-image: '../images/sprite.png';
$icon-02: (0px, 50px, 0px, -50px, 32px, 32px, 84px, 82px, '../images/sprite.png', 'icon_02', );
$icon-03-name: 'icon_03';
$icon-03-x: 42px;
$icon-03-y: 25px;
$icon-03-offset-x: -42px;
$icon-03-offset-y: -25px;
$icon-03-width: 17px;
$icon-03-height: 13px;
$icon-03-total-width: 84px;
$icon-03-total-height: 82px;
$icon-03-image: '../images/sprite.png';
$icon-03: (42px, 25px, -42px, -25px, 17px, 13px, 84px, 82px, '../images/sprite.png', 'icon_03', );
$icon-04-name: 'icon_04';
$icon-04-x: 42px;
$icon-04-y: 0px;
$icon-04-offset-x: -42px;
$icon-04-offset-y: 0px;
$icon-04-width: 42px;
$icon-04-height: 25px;
$icon-04-total-width: 84px;
$icon-04-total-height: 82px;
$icon-04-image: '../images/sprite.png';
$icon-04: (42px, 0px, -42px, 0px, 42px, 25px, 84px, 82px, '../images/sprite.png', 'icon_04', );
$icon-05-name: 'icon_05';
$icon-05-x: 0px;
$icon-05-y: 25px;
$icon-05-offset-x: 0px;
$icon-05-offset-y: -25px;
$icon-05-width: 42px;
$icon-05-height: 25px;
$icon-05-total-width: 84px;
$icon-05-total-height: 82px;
$icon-05-image: '../images/sprite.png';
$icon-05: (0px, 25px, 0px, -25px, 42px, 25px, 84px, 82px, '../images/sprite.png', 'icon_05', );
$spritesheet-width: 84px;
$spritesheet-height: 82px;
$spritesheet-image: '../images/sprite.png';
$spritesheet-sprites: ($icon-01, $icon-02, $icon-03, $icon-04, $icon-05, );
$spritesheet: (84px, 82px, '../images/sprite.png', $spritesheet-sprites, );

// The provided mixins are intended to be used with the array-like variables
//
// .icon-home {
//   @include sprite-width($icon-home);
// }
//
// .icon-email {
//   @include sprite($icon-email);
// }
//
// Example usage in HTML:
//
// `display: block` sprite:
// <div class="icon-home"></div>
//
// To change `display` (e.g. `display: inline-block;`), we suggest using a common CSS class:
//
// // CSS
// .icon {
//   display: inline-block;
// }
//
// // HTML
// <i class="icon icon-home"></i>
@mixin sprite-width($sprite) {
  width: nth($sprite, 5);
}

@mixin sprite-height($sprite) {
  height: nth($sprite, 6);
}

@mixin sprite-position($sprite) {
  $sprite-offset-x: nth($sprite, 3);
  $sprite-offset-y: nth($sprite, 4);
  background-position: $sprite-offset-x  $sprite-offset-y;
}

@mixin sprite-image($sprite) {
  $sprite-image: nth($sprite, 9);
  background-image: url(#{$sprite-image});
}

@mixin sprite($sprite) {
  @include sprite-image($sprite);
  @include sprite-position($sprite);
  @include sprite-width($sprite);
  @include sprite-height($sprite);
}

// The `sprites` mixin generates identical output to the CSS template
//   but can be overridden inside of SCSS
//
// @include sprites($spritesheet-sprites);
@mixin sprites($sprites) {
  @each $sprite in $sprites {
    $sprite-name: nth($sprite, 10);
    .#{$sprite-name} {
      @include sprite($sprite);
    }
  }
}
